.main-header {
  min-width: 100vw;
  min-height: 10rem;
}

.header {
  display: flex;
  margin: 0vh 0vw 0vh 0vw;
  justify-content: space-between;
  // flex-wrap: wrap;
}

.logo {
  display: flex;
  justify-content: center;
}

/* .logo > img {
  width: 50%;
  height: 10%;
  margin: 0vh 0vw 0vh 10vw;
} */

.imp {
  width: 80%;
  height: 50%;
  margin: 5vh -10vw 0vh 10vw;
}

.logo-name {
  padding-left: 10px;
  margin: 5vh 0vw 0vh 10vw;
  display: flex;
  flex-direction: column;
  font-family: Arial;
}

.logo-text1 {
  font-size: 32px;
  color: whitesmoke;
  text-align: center;
}

.logo-text2 {
  text-transform: uppercase;
  font-size: 15px;
  color: whitesmoke;
  text-align: center;
}

.options {
  padding-right: 8vw;
  font-size: 20px;
  // min-width: 5rem;
  color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.opt {
  padding-right: 1rem;
}
